html,
body {
  width: 100%;
  //min-height: 100vh;
  margin: 0;
  padding: 0;
  min-width: 100%;
}

body {
  overflow: hidden;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-family: $font;
  max-width: 100%;
}

* {
  box-sizing: border-box;
}

.sr-only {
  @include sr-only;
}

.container {
  position: relative;
  @include container;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

svg {
  fill: currentColor;
}
.hidden {
  display: none;
}

.only-desktop {
  @include tablet {
    display: none;
  }
}

.only-tablet {
  display: none;

  @include tablet {
    display: block;
  }

  @include mobile {
    display: none
  }
}

.only-mobile {
  @include minTablet {
    display: none;
  }
}

.color {
  &--red {
    color: $orange
  }
}

.font {
  &--tag,
  &--true {
    font-family: $fontTag;
    font-weight: 100;
  }

  &--neue {
    font-family: $font;
  }

  &--title {
    font-style: normal;
    font-family: $fontTitle;
  }

  &--up {
    text-transform: uppercase;
  }
}

img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}

.curtain {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999999;
  background: black;
  overflow: hidden;
}

.wrap-black {
  background: black;
}

[data-main-scroll] {
  width: 100%;
  // overflow: hidden;
}

.c-scrollbar {
  display: none;
}